@import "src/assets/sass/variables";

.resumeBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 50px;
  padding-bottom: 50px;
  .resumeBtnBox {
    display: flex;
    flex-direction: column;
    padding-right: 24px;
    width: 33.3%;
  }
  .resumeComponentBox {
    display: flex;
    width: 66.6%;
    border: 1px solid $border;
    margin-left: 24px;
    min-height: 725px;
  }
}


.resumeBtn {
  line-height: 110%;
  text-transform: uppercase;
  margin: 24px 0;
  display: flex;
  align-items: center;
  outline: none;
  justify-content: center;
  align-content: center;
  background-color: $bgFourth;
  border: 1px solid $line;
  width: 200px;
  font-size: 15px;
  height: 40px;
  font-weight: 700;
  text-align: center;
  letter-spacing: .5px;
  color: $accentText;
  cursor: pointer;
}

.active {
  font-weight: 700;
  background-color: $accentText;
  border: 1px solid $accentText;
  color: $primaryText;
  outline: none;
  cursor: pointer;
}

